/* App.css */

@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;700&display=swap');

body {
  font-family: 'Poppins', sans-serif;
}

.App {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.menu-icon {
  font-size: 24px;
  cursor: pointer;
  background: none;
  border: none;
  color: white;
  position: absolute;
  top: 20px;
  left: 20px;
  z-index: 1000;
}

.content {
  flex: 1;
  padding: 20px;
  transition: margin-left 0.3s ease;
}

.content.sidebar-open {
  margin-left: 250px; /* Adjust this based on the width of the sidebar */
}

body {
  margin: 0;
  padding: 0;
  background: linear-gradient(180deg, NavajoWhite, NavajoWhite);
  color: #000000;
}

.App {
  text-align: center;
}

.header {
  background: darkorange;
  padding: 20px;
  color: white;
  border-bottom: 1px solid #ccc;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
}

.header-right {
  display: flex;
  align-items: center;
  gap: 10px;
}

.profile-link, .cart-link {
  text-decoration: none;
  color: white;
}

.profile-icon, .cart-icon {
  width: 30px;
  height: 30px;
  cursor: pointer;
}

.welcome-message {
  font-size: 1em;
  color: #333;
}

nav a {
  color: blue;
  text-decoration: none;
  margin: 0 15px;
  padding: 10px 15px;
  border-radius: 4px;
  transition: background-color 0.3s ease, transform 0.3s ease;
}

nav a:hover {
  background-color: #1565C0;
  transform: scale(1.1);
}

.section {
  padding: 50px;
  margin: 20px;
  background-color: rgba(255, 255, 255, 0.9);
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
}

.section:hover {
  transform: scale(1.05);
}

.project {
  border: 1px solid #ddd;
  padding: 20px;
  margin: 20px 0;
  background-size: cover;
  background-position: center;
  color: #000000;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.project-title {
  font-weight: bold;
  font-size: 1.4em;
}

a {
  color: #1E88E5;
}

a:hover {
  color: #1565C0;
  text-decoration: underline;
}

h1, h2, h3 {
  font-family: 'Poppins', sans-serif;
  margin: 20px 0;
}

/* Media queries */

/* Mobile styles */
@media (max-width: 600px) {
  .header {
    font-size: 1em;
    padding: 10px;
  }

  .menu-btn {
    font-size: 18px;
  }

  .logo {
    height: 30px;
  }

  h1 {
    font-size: 1.2em;
  }

  .initials {
    font-size: 20px;
  }

  .grid-container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .grid-item {
    width: 100%;
    margin-bottom: 10px;
  }

  .section {
    padding: 20px;
    margin: 10px;
  }
}

/* Tablet styles */
@media (min-width: 601px) and (max-width: 1024px) {
  .header {
    font-size: 1.2em;
  }

  .menu-btn {
    font-size: 20px;
  }

  .logo {
    height: 35px;
  }

  h1 {
    font-size: 1.3em;
  }

  .initials {
    font-size: 22px;
  }

  .grid-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
  }

  .grid-item {
    width: 45%;
    margin-bottom: 20px;
  }

  .section {
    padding: 30px;
    margin: 15px;
  }
}

/* Desktop styles */
@media (min-width: 1025px) {
  .header {
    font-size: 1.5em;
  }

  .menu-btn {
    font-size: 24px;
  }

  .logo {
    height: 40px;
  }

  h1 {
    font-size: 1.5em;
  }

  .initials {
    font-size: 24px;
  }

  .grid-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  .grid-item {
    width: 30%;
    margin-bottom: 30px;
  }

  .section {
    padding: 50px;
    margin: 20px;
  }
}
